<template>
  <div class="hello">
	<div class="ul-container">
		<div class="header" id="head">
			<!-- mail-reply (alias) -->
			<b style="float:left;color: deeppink;"></b>
			<span style="float:right;font-size:16px;"></span>
		</div>
		<!--  -->
		<div class="content">
			<ul class="list" id="list">
				<li v-for="(da,index) in data1">
					<b>
						<input type="checkbox" :checked="da.isChecked" @click="checked(da)">
						<div>
							<i>
								<img :src="da.path" alt="">
							</i>
							<span>
								<p>{{da.name}}</p>
								<!-- <select name="" id="">
									<option value=""></option>
									<option value=""></option>
								</select> -->
								<b>
									<h4>{{da.price | money}} </h4>
									<!--  -->
									<div>
										<button @click="clickM(da,false)">-</button>
										<input type="number" :value="da.num">
										<button @click="clickM(da,true)">+</button>
									</div>
								</b>
							</span>
						</div>
					</b>
					<a href="javascript:void(0);" @click="cover(index)"></a>
				</li>
			</ul>	
		</div>
		<div class="footer" id="foot">
			<div>
				<input type="checkbox" id="all" :checked="selectAllFlag" @click="selectAllGoods">
				<p style="color:orangered;font-size: 20px;">{{totalPrice | money}}</p>
			</div>
			<button></button>
			<!-- <input type="button" id="rev" onclick="rev()"> -->
		</div>
	</div>
	<div class="cart-jscs" v-show="showFlag">
		<i></i>
		<div class="box">
			<div class="tit"><span @click="showFlag = false"></span></div>
			<p>~</p>
			<div class="but"><input name="" type="button" value="" class="over" @click="deleteGood"> 
			<input name="" type="button" value="" class="hy" @click="showFlag = false">
			</div>
		</div>
	</div>
	
	<div class="dex">
		<ul>
			<li>
				<link href="//netdna.bootstrapcdn.com/font-awesome/4.7.0/css/font-awesome.min.css" rel="stylesheet">
	
				<router-link to="/">
					<span class="fa fa-home fa-2x  "></span>
					<p></p>
				</router-link>
			</li>
			<li>
				<router-link to="/found">
					<span class="fa fa-github fa-2x  "></span>
					<p></p>
				</router-link>
			</li>
			<li>
				<router-link to="">
					<span class="fa fa-shopping-cart fa-2x  " style="color:#4BC385;"></span>
					<p style="color:#4BC385;"></p>
				</router-link>
				</li>
			<li>
				<router-link to="my">
					<span class="fa fa-user-circle fa-2x " ></span>
					<p></p>
				</router-link>
			</li>
		</ul>
	</div>
  </div>
</template>

<script>
export default {
  name: 'zhuye',
  data () {
    return {
			showFlag: false,
			deleteId:'',
			totalPrice:0,
			selectAllFlag:false,
			selectList:[],
			 data1:[
			 	{
			 		path:require('@/assets/img/found/6.jpg'),  
			 		name:" 6 130-180g ",
			 		price:49.90,
					num:1,
			 	},
			 	{
			 		path:require('@/assets/img/found/12.jpg'),  
			 		name:" 500g ",
			 		price:269.00,
					num:1,
			 	},
			 	{
			 		path:require('@/assets/img/found/10.jpg'),  
			 		name:" S ",
			 		price:158.00,
					num:1,
			 	},
			 	{
			 		path:require('@/assets/img/found/13.jpg'),  
			 		name:"  1000g   48 ",
			 		price:86.00,
					num:1,
			 	}
			 ],
    }
  },
	methods:{
			//     
			cover(index){
				this.showFlag = true;
				this.deleteId = index;
			},
			//  
			deleteGood(){
				var obj = this.data1[this.deleteId];
				var index = this.selectList.indexOf(obj)
				if(index>0){
					this.selectList.splice(index,1)
				}
				this.data1.splice(this.deletedId,1)
				// 
				this.showFlag = false;
			},
			// 
			clickM(obj,flag){
				if(flag){
					obj.num++;
				}else{
					if(obj.num>1){
						obj.num--;
					}
				}
			},
			// 
			checked(obj){
				if(obj.isChecked == undefined){
					this.$set(obj,'isChecked',true)
				}else{
					obj.isChecked = !obj.isChecked;
				}
				// console.log(obj.isChecked)
				
				if(obj.isChecked){
					this.selectList.push(obj);
				}else{
					// 
					var index = this.selectList.indexOf(obj);
					this.selectList.splice(index,1);
				}
				// console.log(this.selectList)
				if(this.data1.length == this.selectList.length){
					this.selectAllFlag = true;
				}else{
					this.selectAllFlag = false;
				}
			},
			// 
			selectAllGoods(){
				this.selectAllFlag = !this.selectAllFlag;
				// 
				this.selectList = [];
				this.data1.forEach((obj)=>{
				//  isChecked  this.selectAllFlag
				this.$set(obj,'isChecked',this.selectAllFlag)	
				if(this.selectAllFlag){
					// selectList
					this.selectList.push(obj)
				}
				})
			},
			totalPayPrice(){
				// 
				this.totalPrice = 0;
				// 
				this.selectList.forEach((obj)=>{
					this.totalPrice += obj.price*obj.num;
				})
			}
		},
		watch:{
			selectList:{	
				handler(){
					this.totalPayPrice()
				},
				deep:true
			}
		},
		mounted(){
			this.$nextTick(function () {
				this.selectAllGoods()
			})
		}
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
	<style scoped>
	*{margin: 0;padding: 0;}
	li{list-style: none;}
	a{text-decoration: none;}
	.hello{background:#FAEBCC;height:auto;}
	.ul-container{height:auto;width: 98%;margin: 0 auto;position: relative;overflow: hidden;}
	.header{height: 50px;width: 50%;font-size:18px;line-height: 50px;margin:0 20px;color:deeppink;float: right;}

	.list{margin-bottom:98px;}
	.list>li{height:auto;width: 100%;background-color: #FBF6EC;margin: 6px auto;overflow:hidden;border-radius: 10px;}
	.list>li>b{float:left;width:88%;}
	.list>li>b>input{float:left;zoom:160%;margin-top:23px;margin-left:10px;}
	.list>li>a{width:36px;height:22px;float:right;margin:10% auto;color:#4a4949;}

	.list>li>b>div{float:right;width:86%;height:106px;}
	.list>li>b>div>i{width:28%;height:90px;display: block;float:left;}
	.list>li>b>div>i>img{width:100%;height:80px;margin:15px auto;}
	.list>li>b>div>span{width:71%;height:106px;display: block;float:right;}
	.list>li>b>div>span>p{margin:4px 8px;font-size: 13px;font-weight:lighter}
/* .list>li>b>div>span>select{width:96%;background:#cccccc42;color:#b5b4b4e8;border:none;} */
	.list>li>b>div>span>b>h4{color: orangered;font-size:14px;width:38%;/* background: #EE82EE; */display:block;float:left;}
	.list>li>b>div>span>b>div{width:50%;height:25px;/* background: #EE82EE; */display:block;float:right;}
	.list>li>b>div>span>b>div>input{width:30%;display:block;float:left;text-align: center;border:none;color: deeppink;font-size: 13px;}
	.list>li>b>div>span>b>div>button{width:30%;display:block;float:left;background: #cccccc42;border:none;}

	.footer{height: 50px;width: 100%;color: black;font-size:18px;line-height: 50px;position: fixed;left:0;bottom: 73px;background: #FBF6EC;}
	.footer>div{float:left;width:70%;height:50px;}
	.footer>div>input{margin-left:10px;}
	.footer>div>p{float:right;}
	.footer>button{width:20%;font-size:14px;text-align: center;height:34px;border-radius: 34px;float: right;margin-top:8px;border:none;margin-right:5px;background:#F99A1D;}


	.cart-jscs {position: fixed;width: 100%;height: 100%;left: 0;top: 0;z-index: 150;}
	.cart-jscs i {background: #000;width: 100%;height: 100%;left: 0;top: 0;display: block;filter: alpha(opacity=50);-moz-opacity: 0.5;-khtml-opacity: 0.5;opacity: 0.5;}
	.cart-jscs .box {width: 630px;height: 224px;position: fixed;left: 50%;top: 50%;margin-left: -315px;margin-top: -112px;background: #fafafa;}
	.cart-jscs .box .tit {height: 40px;background: url(../assets/img/5.jpg) no-repeat 0 0 #f62648;color: #fff;padding-left: 20px;line-height: 40px;font-size: 14px;font-weight: bold;
	}
	.cart-jscs .box .tit span {width: 40px;height: 40px;display: inline-block;float: right;background: url(../assets/img/5.jpg) no-repeat 0 -40px #f62648;margin-right: 10px;cursor: pointer;}
	.cart-jscs .box p {text-align: center;font-size: 14px;margin-top: 50px;color: #464646;line-height: 1.8;}
	.cart-jscs .box .but {position: absolute;text-align: center;width: 100%;bottom: 25px;}
	.cart-jscs .box .over {height: 32px;width: 100px;display: inline-block;line-height: 32px;background: #f62648;border: 1px solid #f62648;color: #fff;cursor: pointer;}
	.cart-jscs .box .hy {height: 32px;width: 100px;background: #fff;border: 1px solid #CBCBCB;cursor: pointer;}

	.dex{width: 100%;height: 73px;}
	.dex>ul{width: 100%;height: 73px;margin: 0 auto;position: fixed;bottom: 0;left: 0;background: #fff;border-top: 1px solid seagreen;}
	.dex>ul>li{width: 25%;float: left;text-align: center;}
	.dex>ul>li span{margin-top: 10px;color: #8db924;}
	.dex>ul>li p{margin-bottom: 10px;color: #000000;}
</style>



