<style scoped>
	.hello{width: 100%;height: auto;}
	.header{width: 100%;height: 50px;margin: 0 auto;border-bottom: 1px solid #c5b3b3;position: fixed;background: #fff;}
	.header>span{margin-left: 10px;color:#4BC385;}
	.top{width: 69%;height: 30px;display:inline-block;border-radius: 40px;margin: 10px 0 0 10px;border:1px solid #4BC385;}
	
