import Vue from 'vue'
import Router from 'vue-router'
import HelloWorld from '@/components/HelloWorld'
import vegetables from '@/components/vegetables'
import found from '@/components/found'
import my from '@/components/my'
import detail from '@/components/detail'
import fruit from '@/components/fruit'
import fdetail from '@/components/fdetail'
import shop from '@/components/shop'
import adetail from '@/components/adetail'

Vue.use(Router)

export default new Router({
	// mode:'history',
  routes: [
    {
      path: '/',
      name: 'HelloWorld',
      component: HelloWorld
    },
		{
			path: '/vegetables',
			name: 'vegetables',
			component: vegetables
		},
		{
			path: '/found',
			name: 'found',
			component: found
		},
		{
			path: '/my',
			name: 'my',
			component: my
		},
		{
			path: '/detail/:id',
			name: 'detail',
			component: detail
		},
		{
			path: '/fdetail/:id',
			name: 'fdetail',
			component: fdetail
		},
		{
			path: '/adetail/:id',
			name: 'adetail',
			component: adetail
		},
		{
			path: '/fruit',
			name: 'fruit',
			component: fruit
		},
		{
			path: '/shop',
			name: 'shop',
			component: shop
		},
  ]
})



